.point-info-content {
    width: auto;
    height: fit-content;
    padding: 20px 20px 20px 20px;
    user-select: text;
}
.point-info-content div{
    margin-bottom: 10px;
}

.position > div[id^=GPpointInfo-] > dialog[id^=GPpointInfoPanel] {
    position: absolute;
}

/* Showing/hiding */

button[id^=GPshowPointInfoPicto][aria-pressed="false"] + dialog {
    display: none;
    visibility: hidden;
    opacity: 0%;
}

button[id^="GPshowPointInfoPicto-"][aria-pressed="true"] + dialog {
    display: flex;
    flex-direction: column;
    visibility: visible;
    opacity: 100%;
    width: 350px;
    height: initial;
    max-height: inherit;
}
button[id^="GPshowPointInfoPicto-"] {
    display: none;
}


.ol-ctx-menu-container {
    border-radius: 0 !important;
    font-size: 0.75rem !important;
    z-index: 1800;
}
.ol-context-menu-custom li {
    line-height: 2em !important;
}
.ol-ctx-menu-separator hr {
    background-image: none !important;
}


/* CSS ol-contextmenu */

.ol-ctx-menu-container {
    position: absolute;
    padding: 8px;
    color: #222;
    font-size: 13px;
    border-radius: 5px;
    box-shadow: #0003 3px 3px 5px;
    box-sizing: border-box
  }
  
  .ol-ctx-menu-container div,
  .ol-ctx-menu-container span,
  .ol-ctx-menu-container a,
  .ol-ctx-menu-container img,
  .ol-ctx-menu-container ul,
  .ol-ctx-menu-container li {
    margin: 0;
    padding: 0;
    border: 0;
    font: inherit;
    font-size: 100%;
    vertical-align: baseline
  }
  
  .ol-ctx-menu-container a img {
    border: none
  }
  
  .ol-ctx-menu-container *,
  .ol-ctx-menu-container *:before,
  .ol-ctx-menu-container *:after {
    box-sizing: inherit
  }
  
  .ol-ctx-menu-container.ol-ctx-menu-hidden {
    opacity: 0;
    visibility: hidden;
    transition: visibility 0s linear .3s, opacity .3s
  }
  
  .ol-ctx-menu-container ul {
    list-style: none
  }
  
  .ol-ctx-menu-container li {
    position: relative;
    line-height: 20px;
    padding: 2px 5px;
    white-space: nowrap
  }
  
  .ol-ctx-menu-container li:not(.ol-ctx-menu-separator):hover {
    cursor: pointer;
    background-color: #333;
    color: #eee
  }
  
  .ol-ctx-menu-container li.ol-ctx-menu-submenu .ol-ctx-menu-container {
    border: 1px solid #eee;
    padding: 8px;
    top: 0;
    opacity: 0;
    visibility: hidden;
    transition: visibility 0s linear .3s, opacity .3s
  }
  
  .ol-ctx-menu-container li.ol-ctx-menu-submenu:after {
    position: absolute;
    top: 7px;
    right: 10px;
    content: "";
    display: inline-block;
    width: .6em;
    height: .6em;
    border-right: .3em solid #222;
    border-top: .3em solid #222;
    transform: rotate(45deg)
  }
  
  .ol-ctx-menu-container li.ol-ctx-menu-submenu:hover:after {
    border-color: #eee
  }
  
  .ol-ctx-menu-container:not(.ol-ctx-menu-hidden) li.ol-ctx-menu-submenu:hover>.ol-ctx-menu-container {
    opacity: 1;
    visibility: visible;
    transition-delay: 0s
  }
  
  .ol-ctx-menu-container li.ol-ctx-menu-separator {
    padding: 0
  }
  
  .ol-ctx-menu-container li.ol-ctx-menu-separator hr {
    border: 0;
    height: 1px;
    background-image: linear-gradient(to left, #0000, #000000bf, #0000)
  }
  
  .ol-ctx-menu-icon {
    text-indent: 20px;
    background-size: 20px auto;
    background-repeat: no-repeat;
    background-position: left center
  }
  
  .ol-ctx-menu-zoom-in {
    background-image: url(data:image/png;base64,)
  }
  
  .ol-ctx-menu-container li:hover.ol-ctx-menu-zoom-in {
    background-image: url(data:image/png;base64,)
  }
  
  .ol-ctx-menu-zoom-out {
    background-image: url(data:image/png;base64,)
  }
  
  .ol-ctx-menu-container li:hover.ol-ctx-menu-zoom-out {
    background-image: url(data:image/png;base64,)
  }